import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { Card, Col, Row } from 'react-bootstrap';

const Cards = () => {

  const [blogpostost, setBlogpost] = useState([]);

  useEffect(() => {
    axios({
      method: "GET",
      url: "http://127.0.0.1:8000/api/blogpost/"
    }).then(response => {
      setBlogpost(response.data);
    });
  }, []);
    
  return ( 
    <Row md={3} >
      {blogpostost.map(post => (
        <Col>
          <Card className="mb-4" key={post.id}>
              {/* <Card.Img variant="top" src={post.image} /> */}
              <Card.Body>
                  <Card.Title>{ post.title }</Card.Title>
                  <Card.Text>{ post.content }</Card.Text>
              </Card.Body>
              <Card.Footer className="text-muted" style={{fontSize: '12px'}}><Link to={{pathname: `/api/category/${post.blog_category.id}`}}>#{post.blog_category.slug}</Link> ({post.pub_date})</Card.Footer>
          </Card>        
        </Col>))}
    </Row>
  );
}

export default Cards;
